
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import axios from 'axios'

export default {
components: {
XHeader,
Group,
XInput,
PopupPicker,
XSwitch,
XButton,
Datetime,
CellFormPreview,
Cell
},
methods: {
toPay: function() {
if (this.name !== '' && this.idNum !== '' && this.telNum !== '') {
var telNum = { telNum: this.telNum };
axios.post('http://localhost:7001/payNumFind', telNum).then((response) => {
if (response.data.data === '已注册用户') {
localStorage.name = this.name;

location.href = '/#/pay';
}
if (response.data.data === '未注册用户') {
alert(response.data.data);
}
});
} else {
alert('请将乘客信息填写完整！');
}
}
},
data() {
return {
name: '',
idNum: '',
telNum: '',
people: ['成人票（12岁以上）'],
card: ['身份证'],
list1: [['成人票（12岁以上）', '儿童票']],
list2: [['身份证', '军官证', '护照']],
list: [
{
label: "始发站： " + localStorage.setout,
value: "有票"
},
{
label: "终点站： " + localStorage.arrive,
value: "普通座"
}
],
readonly: true,
valueReadonly: localStorage.Data,
ticketTime: localStorage.time + ' 发车',
ticketPrice: '￥' + localStorage.price
}
}
}



