
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import axios from 'axios'

export default {
components: {
XHeader,
XInput,
Group,
XButton,
Flexbox,
FlexboxItem
},
methods: {
signIn: function() {
var data = {
telNum: this.telNum,
password: this.password
};
axios.post("http://localhost:7001/adds", data).then((response) => {
if (response.data.data === '登录成功') {
localStorage.telNum = this.telNum;
location.href = '/';
return;
}
if (response.data.data === '用户未注册或用户名/密码输入错误') {
alert (response.data.data);
return;
}
});
}
},
data () {
return {
telNum: '',
password: ''
}
}
}



