
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import axios from 'axios'

export default {
components: {
XHeader,
PopupPicker,
Group,
Calendar,
InlineCalendar,
Tabbar,
TabbarItem,
XButton,
Cell
},
methods: {
signIn: function() {
location.href = '../#/SignIn';
},
search: function() {
var bus = {
setout: this.setout[0],
arrive: this.arrive[0]
}
localStorage.setout = this.setout[0];
localStorage.arrive = this.arrive[0];
localStorage.Data = this.data;
axios.post('http://localhost:7001/searchBus', bus).then(function(response) {
if (response.data.data === '查询成功') {
location.href = '/#/ticket';
return;
}
if (response.data.data === '未查询到当前车次') {
alert(response.data.data);
}
});
}
},




data() {
return {
setoutCity: [
[
"安居区",
"安岳县",
"璧山县",
"白玉县",
"北川县",
"巴中市",
"重庆市",
"成都市"
]
],
setout: [""],
arrive: [""],
data: "TODAY",
showLastMonth: true,
showNextMonth: true
};
}
};



