
import XHeader from 'vux/src/components/x-header/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import axios from "axios";

export default {
components: {
XHeader,
FormPreview,
Datetime,
Group,
CellFormPreview,
Cell
},
data() {
return {
list: [
{
label: "始发站： " + localStorage.setout,
value: "余票： " + parseInt(Math.random() * 30) + "张"
},
{
label: "终点站： " + localStorage.arrive,
value: "普通座"
}
],
tickets: [],
readonly: true,
valueReadonly: localStorage.Data
};
},
methods: {
pay: function(ticket) {
localStorage.time = ticket.time;
localStorage.price = ticket.price;
location.href = "/#/ticketInfo";
}
},
async mounted() {
var setout = localStorage.setout;
var arrive = localStorage.arrive;
var bus = {
setout: setout,
arrive: arrive
};
axios.post("http://localhost:7001/showBus", bus).then(response => {
this.tickets = response.data;
});
}
};



