
import XHeader from 'vux/src/components/x-header/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import axios from "axios";

export default {
components: {
XHeader,
FormPreview,
Datetime,
Group,
CellFormPreview,
Cell,
XButton
},
data() {
return {
tickets: [],
readonly: true
};
},
async mounted() {
var telNum = { telNum: localStorage.telNum };
axios.post("http://localhost:7001/findTicket", telNum).then(response => {
this.tickets = response.data;
});
}
};



