
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Range from 'vux/src/components/range/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'


export default {
components: {
XHeader,
Group,
Cell,
Rater,
Range,
XTextarea,
CheckIcon,
XButton
},
methods: {
showModule () {
AlertModule.show({
title: '评价成功！',
content: ('感谢您的评价'),
onShow () {
console.log('Module: I\'m showing')
},
onHide () {
console.log('Module: I\'m hiding now')
location.href = '/';
}
})
},

},
data() {
return {
AssessNum1: 5,
AssessNum2: 5,
AssessNum3: 5,
demo1: false,
show: false
}
}
}



